import axios from "axios";
//import $ from 'jquery'
import { Message, Loading } from "element-ui";
import Bus from "./bus.js";
import Router from "../router";
import { getAuthorization } from "@/utils/common.js";
// let baseUrl = "http://47.103.17.219:39106";
let baseUrl = "http://127.0.0.1:8002";
// let baseUrl = "http://127.0.0.1:8002";
// let baseUrl = "/api";

axios.interceptors.response.use(
  function(response) {
    let authorization = response.headers.token;
    let url = response.config.url;
    if (authorization && url.indexOf("/user/login") != "-1") {
      localStorage.setItem("wisdomAuthorization", authorization);
    }
    return response;
  },
  function(error) {
    return Promise.reject(error);
  }
);

//请求数据
function requestData(url, queryParams, successCallback) {
  let orExport = false;
  switch (url) {
    case "/dataList/export":
    case "/site/export":
      orExport = true;
      break;
  }
  let that = this;
  let authorization = getAuthorization();
  let header = {
    "Content-Type": "application/x-www-form-urlencoded"
  };
  if (authorization) {
    header.token = authorization;
  }
  let method = {
    params: queryParams,
    headers: header
  };
  if (orExport) {
    method.responseType = "blob";
  }
  axios
    .get(baseUrl + url, method)
    .then(function(res) {
      successCallback(res.data);
    })
    .catch(function(error) {
      console.log("error", error);
    });
}
//Post请求数据
function requestPostxxxData(url, queryParams, successCallback) {
  let orExport = false;
  let that = this;
  let header = {
    "Content-Type": "application/x-www-form-urlencoded"
  };
  let authorization = getAuthorization();
  if (authorization) {
    header.token = authorization;
  }
  axios
    .post(baseUrl + url, queryParams, {
      headers: header
    })
    .then(function(res) {
      if (res.data.code == "0") {
        successCallback(res.data.datas);
      } else {
        Message.error(res.data.msg);
      }
    })
    .catch(function(error) {
      console.log("error", error);
    });
}
