import Bus from '@/utils/bus.js';
import {
  mapGetters
} from 'vuex';
//import router from '../router';
import {
  temp_humidity
} from '../../utils/service';
import {
  resetRouter
} from '../../router/restrouter'
import mqtt from "mqtt"
export default {
  name: 'headers',
  data() {
    return {
      humidity: '',
      temp: '',
      nowDate: null, //存放年月日变量
      nowTime: null, //存放时分秒变量
      timer: "", //定义一个定时器的变量
      currentTime: new Date(), // 获取当前时间
      getWeek: '', //当前星期
      href: ''
    }
  },
  created() {
    this.timer = setInterval(this.getTime, 1000);
    this.initMqtt()
  },
  computed: {

  },
  components: {},
  mounted() {
    this.sTblist()
    if (window.history && window.history.pushState) {
      // 向历史记录中插入了当前页
      history.pushState(null, null, document.URL);
      window.addEventListener('popstate', this.goBack, false);
    }
  },
  methods: {
    initMqtt(topic) {
      const mqttConfig = {
        port: 8083,
        keepalive: 60,
        clientId: 'mqttjs_' + Math.random().toString(16).substr(2, 8),
        // protocolId: 'MQTT',
        // protocolVersion: 4,
        // clean: true,
      }
      var TopicList = {
        "meteorological": "topic/mqtt/meteorological",
        // "devicestatistics": "topic/mqtt/devicestatistics",
        // "oilpoolstate": "topic/mqtt/oilpoolstate",
        // "smoke_check": "topic/mqtt/smoke_check"
      }
      const _this = this;
      //建立连接，需要搭建消息中间件服务器的（我这里是后台）提供用户名或密码
      const cfg = Object.assign(mqttConfig, {
        // username: 'xxxx1',
        // password: 'xxx2',
      });
      // 在data中定义MQTT,以便断开连接
      // ws://xxxxx是连接地址，后台提供，wss是https连接
      _this.MQTT = mqtt.connect('ws://8.142.173.95:8083/mqtt', cfg)
      _this.MQTT.on('error', (e) => {
        _this.MQTT.end();
      });
      //建立连接后订阅主题
      _this.MQTT.on('connect', () => {
        // 订阅一个主题
        for (let k in TopicList) {
          const element = TopicList[k];
          _this.MQTT.subscribe(element, {
            qos: 1
          }, err => {
            if (!err) {
              // console.info(' ---- 订阅成功')
            } else {
              // console.warn('订阅失败')
            }
          })
        }
      });
      // 后台发送的消息
      _this.MQTT.on('message', function (top, message) {
        //发送过来的如果是对象你是需要解析的。
        const res = JSON.parse(message.toString());
        const TOP = top;
        // console.log(top + ': ', res);
        //根据不同主题进行赋值操作
        switch (TOP) {
          case "topic/mqtt/meteorological":
            var slist = res.data[0]
            _this.$nextTick(() => {
              _this.temp = slist.temp
              _this.humidity = slist.humidity
            })
            break;
            // case "topic/mqtt/devicestatistics":
            //   break;
            // case "topic/mqtt/oilpoolstate":
            //   break;
            // case "topic/mqtt/smoke_check":
            //   break;
        }
      });
      // 其他异常事件
      _this.MQTT.on('reconnect', () => {
        // console.info('正在重连')
      });
      _this.MQTT.on('disconnect', (error) => {
        // console.info('服务器断开:', error)
      });
      _this.MQTT.on('close', () => {
        _this.MQTT.end();
      });
    },
    sTblist() {
      temp_humidity({}, res => {
        this.temp = res.data[0].temp
        this.humidity = res.data[0].humidity
      })
    },
    getTime() {
      const date = new Date();
      const year = date.getFullYear();
      const month = date.getMonth() + 1;
      const day = date.getDate();
      const hour = date.getHours();
      const minute = date.getMinutes();
      const second = date.getSeconds();
      const week = new Date().getDay();
      const str = ''
      if (this.hour > 12) {
        this.hour -= 12;
        this.str = " AM";
      } else {
        this.str = " PM";
      }
      this.month = check(month);
      this.day = check(day);
      this.hour = check(hour);
      this.minute = check(minute);
      this.second = check(second);

      const weeks = ["日", "一", "二", "三", "四", "五", "六"];
      const getWeek = "星期" + weeks[week];
      this.getWeek = getWeek

      function check(i) {
        const num = (i < 10) ? ("0" + i) : i;
        return num;
      }
      // this.nowDate = year + "年" + this.month + "月" + this.day + "日";
      // this.nowTime = this.hour + ":" + this.minute + ":" + this.second + this.str;
      this.nowDate = year + "-" + this.month + "-" + this.day;
      this.nowTime = this.hour + ":" + this.minute + ":" + this.second;
    },
    // 点击退出按钮
    handleExit() {
      // console.log('this.$router :>> ', JSON.parse(window.localStorage.getItem("userInfo")));
      localStorage.clear();

      // this.$router.push({
      //   path: "/login"
      // });
      // this.$router.replace({
      //   path: '/'
      // });
      history.pushState(null, null, document.URL);
      this.href = '/'
      location.reload();
    },
    goBack() {
      // console.log("点击了浏览器的返回按钮");
      history.pushState(null, null, document.URL);
    },
  },
  beforeDestroy() {
    if (this.timer) {
      clearInterval(this.timer); // 在Vue实例销毁前，清除定时器
    }
    if (this.MQTT.end) this.MQTT.end();
  },
  destroyed() {
    resetRouter(); //退出系统，重置路由
  },
  destroyed() {
    window.removeEventListener('popstate', this.goBack, false);
  },
}



